import { connect } from "react-redux";
import * as Actions from "./actions/actions";
import React from "react";
import Header from "./content/header";
import Footer from "./content/footer";
import { Route, Switch, Router } from 'react-router-dom';
import home from './content/body_content/home';
import aboutUs from './content/body_content/about_us';
import services from './content/body_content/services';
import contactUs from './content/body_content/contact_us';
import { createBrowserHistory as createHistory } from "history";

class Components extends React.Component {
    history = createHistory(this.props);
    render() {
        return (
            <div>
                <Header/>
                <Router history={this.history}>
                    <Switch>
                        <Route exact path="/" component={home} />
                        <Route exact path="/about_us" component={aboutUs} />
                        <Route exact path="/services" component={services} />
                        <Route exact path="/contact_us" component={contactUs} />
                    </Switch>
                </Router>
                <Footer/>                
            </div>
        );
    }
}

Components.propTypes = {
};

function mapStateToProps(state) {
    return state;
}

const VisibleConfigs = connect(
    mapStateToProps,
    Actions
)(Components);

export default VisibleConfigs;



